



















































.bottom-nav-bar {
  .nav {
    height: 120px;
    width: 100%;
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    position: fixed;
    bottom: 0;
    z-index: 9999;
    padding: 0 7vw;
    border-top: 0.5px solid #d8d8d8;
    .nav-item {
      width: 100px;
      text-align: center;
      line-height: 50px;
    }
    img {
      height: 50px;
      width: 50px;
      margin-top: 8px;
    }
  }
  .router-link-active {
    color: #e54847;
  }
}
